.Province {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
   
    .flex { 
        width: 100%;
        // display: flex;
        // align-items: center;
        // justify-content: center;
        margin-top: 100px;
        .content {
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            margin-top: 50px;
            flex-direction: column;
            .title {
                .title-h2 {
                    margin-bottom: 40px;
                    h2 {
                        color: rgb(0, 153, 153,1);
                    }
                }
                .title-content {
                    p {
                        margin-top: 10px;
                    }
                }
            }
            .body-content {
                width: 100%;
                margin-top: 20px;
                .body-img {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    position: relative;
                    width: 100%;
                    img {
                        width: 80%;
                        border-radius: 10px;
                    }
                    .body-item {
                        position: absolute;
                        left: 0;
                        top: 20px;
                        .item {
                            padding: 20px;
                            margin-bottom: 10px;
                            min-width: 300px;
                            display: flex;
                            align-items: center;
                            justify-content: flex-start;
                            width: 80%;
                            gap: 15px;
                            min-height: 50px;
                            border-radius: 30px;
                            background-color: antiquewhite;
                        }
                    }
                }
            }
            .last-content {
                margin-top: 50px;
                padding: 10px;
                h2 {
                    color: rgb(0, 153, 153);
                    margin-bottom: 20px;
                }
                p {
                    padding: 10px;
                }
                strong {
                    font-size: 17px;
                    color: rgb(43, 41, 41);
                }
                em {
                    color: black;
                }
            }
        }
    }
}

@media screen and (max-width: 730px)  {
    .Province {
        .flex { 
            .content {
                .body-content {
                    
                    .body-img {
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        position: relative;
                        img {
                            width: 100%;
                        }
                        .body-item {
                            position: absolute;
                            right: -25%;
                            .item {
                                margin-bottom: 10px;
                                display: flex;
                                align-items: center;
                                justify-content: flex-start;
                                width: 80%;
                                gap: 10px;
                                height: auto;
                                padding: 10px;
                                border-radius: 30px;
                                background-color: antiquewhite;
                            }
                        }
                    }
                }
            }
        }
    }
}
