@media screen and (max-width: 600px) {
    .Province {
        .flex { 
            .content {
                margin-top: 0;
                .title {
                    padding: 10px;
                }
                .body-content {
                    
                    .body-img {
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        position: static;
                        flex-direction: column-reverse; 
                        img {
                            width: 100%;
                            display: none;
                        }
                        .body-item {
                            width: 100%;
                            position: static;
                            display: grid;
                            grid-template-columns: repeat(2,1fr);
                            left: 0;
                            // width: 80%;
                            margin:  20px 0;
                            justify-items: center;
                            .item {
                                max-width: 300px;
                                min-width: 110px;
                                display: block;
                                text-align: center;
                                width: 90%;
                                padding: 10px;
                                
                            }
                        }
                    }
                }
            }
        }
    }
}
